:root {
	--peachpay-theme-color: #21105d;
	--peachpay-theme-color-opaque: #21105d80;
	--peachpay-theme-color-light: #21105d20;
	--peachpay-test-mode-color: #{$orchColorOrange800};
}

* {
	box-sizing: border-box;
}

body {
	position: relative;
	margin: 0;
	padding: 1rem 0 0;
	width: calc(100vw - (100vw - 100%));
	height: 100vh;

	@supports (height: 100dvh) {
		height: 100dvh;
	}

	@supports (width: 100dvw) {
		width: calc(100dvw - (100dvw - 100%));
	}

	// stylelint-disable-next-line max-line-length
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Noto Sans', Ubuntu, Cantarell, 'Helvetica Neue', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
	font-feature-settings: 'salt' on, 'liga' off;
	-webkit-font-smoothing: antialiased;

	@media screen and (max-width: 1054px) {
		width: 96%;
	}

	@media screen and (max-width: 900px) {
		justify-content: flex-start;
		width: 100%;
		margin: 0 !important;
		padding: 0 !important;
	}
}

img {
	user-select: none;
}

.pp-content {
	background-color: white;
	border-radius: 8px;
	width: 1000px;
	position: relative;
	margin: auto;

	.pp-container-new & {
		@media screen and (max-width: 1054px) {
			width: 100%;
		}

		@media only screen and (max-width: 900px) {
			border-radius: 0;
			box-sizing: border-box;
			margin: 0;
			top: 0;
			height: 100%;
			overflow-y: auto;
			overflow-x: hidden;
		}
	}
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: normal;
	margin-bottom: 0.55em;
}

h1 {
	font-size: 1.5rem;
	margin: 0 0 1rem;
}

h2 {
	font-size: 1.25rem;
	margin: 0.25rem 0;
}

a {
	text-decoration: none;
	color: #1068bf;
}

.pay-btn,
.free-btn,
#pp-continue-to-shipping,
#pp-continue-to-payment,
#pp-continue-to-payment-mobile,
#pp-continue-to-shipping-mobile {
	font-size: 16px;
}

.pp-close-x {
	position: absolute;
	top: 8px;
	right: 8px;
	z-index: 3;
	font-size: larger;
	color: black;
	transition: color 0.3s;

	&:hover {
		color: red;
	}
}

$form-gap: 10px;

.pp-form {
	display: flex;
	flex-wrap: wrap;
	gap: $form-gap;
	padding: 0;
	margin: 0;
	border: none;
	outline: none;
}

.pp-fw-30 {
	width: calc(30% - ($form-gap / 2));
}

.pp-fw-50 {
	width: calc(50% - ($form-gap / 2));
}

.pp-fw-70 {
	width: calc(70% - ($form-gap / 2));
}

.pp-fw-100 {
	width: calc(100%);
}

.w-30 {
	width: 30%;
}

.w-50 {
	width: 50%;
}

.w-70 {
	width: 70%;
}

.w-80 {
	width: 80%;
}

.w-100 {
	width: 100%;
}

.h-100 {
	height: 100%;
}

.flex-container {
	display: flex;
	flex-direction: row;
}

.flex-left {
	width: 50%;
	flex-shrink: 0;
	flex-grow: 0;
}

.flex-right {
	width: 50%;
	flex-shrink: 0;
	flex-grow: 0;
	background-color: #f9f9f9;
	border-top-right-radius: 8px;
	border-bottom-right-radius: 8px;
}

.logo-container-mobile-view {
	display: none;
	justify-content: flex-end;
	padding: 8px;
	margin-top: 8px;
	border-top: 1px solid #c5c5c5;

	.pp-container-new & {
		@media screen and (max-width: 900px) {
			display: flex;
		}
	}
}

.logo-container,
.logo-container-mobile-view {
	.pp-tos-privacy-container {
		color: white !important;
		display: flex;
		align-items: center;

		& > * {
			display: flex;
		}

		.pp-tos {
			color: white;
			font-size: x-small;
		}

		.pp-privacy {
			color: white;
			margin-right: 1rem;
			font-size: x-small;
		}
	}

	@mixin logo-container-on-white {
		.powered-by {
			color: black;
			font-size: x-small;
			vertical-align: top;
		}

		.pp-tos-privacy-container {
			font-size: x-small;
			vertical-align: text-top;

			.pp-tos {
				color: black;
			}

			.pp-privacy {
				color: black;
				margin-right: 10px;
			}
		}
	}

	.pp-container-new & {
		@media screen and (max-width: 900px) {
			@include logo-container-on-white;
		}
	}
}

.logo-container {
	width: 1000px;
	margin: 5px auto;
	display: flex;
	justify-content: flex-end;
	align-items: center;

	.pp-container-new & {
		@media screen and (max-width: 1054px) {
			width: 100%;
		}

		@media screen and (max-width: 900px) {
			display: none;

			.powered-by {
				color: black;
				font-size: x-small;
				vertical-align: top;
			}

			.pp-tos-privacy-container {
				font-size: x-small;
				vertical-align: text-top;

				.pp-tos {
					color: black;
				}

				.pp-privacy {
					color: black;
					margin-right: 1rem;
				}
			}
		}
	}
}

.pp-form-label {
	color: #828282;
	display: block;
	transform-origin: 0 0;
	transition: all 0.3s;
	position: absolute;
	cursor: text;
	pointer-events: none;
}

.wc-coupon-code .pp-form-label,
.pw-wc-gift-card .pp-form-label {
	font-size: 16px;
	line-height: 26px;
	transform: translate3d(12px, 7px, 0);
}

@media (max-width: 500px) {
	.pp-form-label.mobile {
		font-size: 12px;
	}
}

.pp-form .pp-form-label {
	pointer-events: none;
	font-size: 16px;
	line-height: 26px;
	transform: translate3d(16px, 11px, 0);
	color: #707070;
	font-weight: 400;
}

.pp-new-field .pp-form-label {
	font-size: 14px;
	transform: translate3d(16px, 8px, 0);
}

.pp-w30-label {
	width: 65px;
	height: 23.3px;
	overflow: hidden;
}

.pp-new-password-box {
	background-color: #e5e5e5;
}

.pp-radio-label {
	display: contents;
	transform: translate3d(1.75rem, 0.7rem, 0) scale(0.8);
}

.pp-info-form-header {
	display: flex;
	color: #212121;
	margin: 12px 0 0 2px;
	font-weight: 500;
	font-size: 18px;
}

.pp-form textarea.text-input {
	resize: vertical;
	font-family: inherit;
	min-height: 80px;
	max-height: 200px;
}

.pp-form .text-input,
.pp-form select,
.wc-coupon-code-input.text-input,
.wc-gift-card-input.text-input {
	transition: border 0.3s, color 0.3s, background-color 0.3s;
	outline: none;
	padding: 24px 12px 8px;
	color: #43454b;
	appearance: none;
	font-weight: normal;
	font-size: 16px;
	box-shadow: none;
	border-radius: 4px;
	background-color: #f4f4f4;
	margin: -1px;
	display: flex;
	width: 100%;
	text-overflow: ellipsis;
	overflow: hidden;
	border: 1px solid white;
}

.wc-coupon-code-input.text-input,
.wc-gift-card-input.text-input {
	padding: 14px 8px 2px;
	border-radius: 4px 0 0 4px;
	display: flex;
	width: 100%;
	margin: 0;
}

.pp-form .text-input:focus,
.pp-form select:focus,
.wc-coupon-code-input.text-input,
.wc-gift-card-input.text-input {
	border: 1px solid var(--peachpay-theme-color);
	background-color: white;
}

.pp-form .text-input:focus + .pp-form-label,
.pp-form .text-input:not(:placeholder-shown) + .pp-form-label,
.pp-form .pp-select-label {
	transform: translate3d(12px, 3px, 0) scale(0.8);
	font-weight: 600;
}

.wc-coupon-code-input.text-input:focus + .pp-form-label,
.wc-coupon-code-input.text-input:not(:placeholder-shown) + .pp-form-label,
.wc-gift-card-input.text-input:focus + .pp-form-label,
.wc-gift-card-input.text-input:not(:placeholder-shown) + .pp-form-label {
	transform: translate3d(8px, 2px, 0) scale(0.7);
}

.pp-form .text-input:focus + .pp-form-label {
	color: var(--peachpay-theme-color);
}

.text-input:focus + .pp-w30-label,
.text-input:not(:placeholder-shown) + .pp-w30-label {
	transform: translate3d(16px, 8px, 0) scale(0.8);
	width: 100px;
	height: 23.3px;
}

@media only screen and (max-width: 900px) {
	h1 {
		font-size: 1.25rem;
		margin: 0;
	}

	h2 {
		font-size: 1.5rem;
		margin: 10px 0.25rem;
	}

	.pp-hide-for-mobile {
		display: none !important;
	}

	.flex-container {
		display: flex;
		flex-direction: column;
	}

	.flex-left {
		display: flex;
		flex-direction: column;
		width: 100%;
		padding: 24px 16px 0 !important;
	}

	.flex-right {
		display: flex;
		flex-direction: column;
		width: 100%;
		padding: 0 16px !important;
		border-radius: 0 !important;
		background-color: transparent;
	}

	#pp-checkout {
		margin: 0;
		padding-bottom: 0;
	}

	.pp-order-summary {
		display: none;
	}

	.text-input:focus + .pp-form-label,
	.text-input:not(:placeholder-shown) + .pp-form-label,
	.pp-select-label {
		transform: translate3d(16px, 8px, 0) scale(0.8);
	}

	.text-input:focus + .pp-w30-label,
	.text-input:not(:placeholder-shown) + .pp-w30-label {
		transform: translate3d(16px, 8px, 0) scale(0.8);
		width: 100px;
		height: 23.3px;
	}

	.pp-slide-up-inner {
		width: 100% !important;
	}
}

.no-wrap {
	white-space: nowrap;
}

.text-ellipsis {
	overflow: hidden;
	text-overflow: ellipsis;
}

.relative {
	position: relative;
}

.flex {
	display: flex;
}

.flex-wrap {
	display: flex;
	flex-wrap: wrap;
}

.flex-row {
	flex-direction: row;
}

.col {
	flex-direction: column !important;
}

.pp-ai-center {
	align-items: center;
}

.pp-gap-2 {
	gap: 2px;
}

.pp-gap-4 {
	gap: 4px;
}

.pp-gap-6 {
	gap: 6px;
}

.center {
	text-align: center;
}

.block {
	display: block;
}

.inline-block {
	display: inline-block;
}

.hide {
	display: none !important;
}

.pp-section-mt {
	margin-top: 32px;
}

.pp-section-mb {
	margin-bottom: 32px;
}

#pp-shipping-options {
	gap: 6px;
	margin-top: 16px;
	display: flex;
	flex-direction: column;
}

.mt-half {
	margin-top: 0.5rem;
}

.mb-half {
	margin-bottom: 0.5rem !important;
}

.mb-1 {
	margin-bottom: 1rem;
}

.pp-modal-col {
	padding: 32px 24px 12px;
	display: flex;
	flex-direction: column;
	transition: all 0.3s;
}

.rounded {
	border-radius: 5px;
}

.lightgrey {
	background-color: lightgrey;
}

.pp-container-new .pp-dark-blur {
	filter: blur(2px);
	pointer-events: none;
}

.btn {
	border: none;
	border-radius: 8px;
	color: white;
	text-transform: none !important;
	font-weight: bold;
	padding: 16px;
	width: 15rem;
	line-height: inherit;
	cursor: pointer;
	background-color: var(--peachpay-theme-color);
	align-self: center;
	display: flex;
	gap: 4px;
	justify-content: center;
	align-items: center;
}

.btn-shadow {
	box-shadow: 0 5px 5px -5px var(--peachpay-theme-color);
}

.btn:hover,
.btn:disabled {
	color: #fff !important;
	opacity: 0.75;
}

.btn:disabled {
	cursor: not-allowed;
}

.btn:focus {
	color: white !important;
	border-color: transparent !important;
	opacity: 0.75;
}

.spinner {
	width: 20px;
	vertical-align: middle;
}

.button-text {
	vertical-align: middle;
}

.pp-summary {
	width: 100%;
	border-collapse: collapse;
}

dd::after {
	content: '\A';
	white-space: pre;
}

dt,
dd,
dd p {
	color: #757575 !important;
	display: inline;
	margin: 0;
}

.muted {
	color: rgb(0 0 0 / 55%);
	font-size: smaller;
}

.bold {
	font-weight: bold !important;
}

.x-large {
	font-size: x-large;
}

.pp-title {
	display: flex;
	font-size: 16px;
	line-height: 20px;
	font-weight: 600;
	margin: 0 0 10px;
	vertical-align: middle;
	transition: all 0.3s;
}

.pp-address-header {
	display: flex;
	min-width: fit-content;
	margin-left: 1px;
	margin-right: 4px;
}

.pp-recipient-text {
	margin-right: 1px;
	display: inline-block;
	font-weight: 400;
}

.pp-edit-checkbox {
	border-radius: 5px;
	padding: 0.5rem;
	white-space: nowrap;
}

#pp-shipping-edit:hover + label.pp-edit-checkbox,
#pp-shipping-edit:checked + label.pp-edit-checkbox {
	cursor: pointer;
	background-color: var(--peachpay-theme-color-light);
}

.order-summary-table {
	display: block;
	margin-bottom: 1rem;
}

.order-summary-list {
	display: block;
	height: auto;
	max-height: 20rem;
	width: 100%;
	overflow-y: overlay;
}

.order-summary-list::-webkit-scrollbar {
	width: 12px;
	padding: 0 4px;
	display: flex;
}

.order-summary-list::-webkit-scrollbar-track {
	background-color: #fafafa;
	box-shadow: 1px 1px 2px 2px #f5f5f5 inset;
	border-radius: 100px;
}

.order-summary-list::-webkit-scrollbar-thumb {
	background: #c1c1c1;
	border: 3px solid #fafafa;
	border-radius: 6px;
}

.pp-top-section {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 32px;
	gap: 16px;
	flex-wrap: wrap;
}

.pp-top-section.mobile {
	display: none;
	height: auto;
}

.pp-top-corner {
	display: flex;
	height: max-content;
	gap: 12px;
	align-items: center;
}

.pp-top-corner.mobile {
	flex-wrap: wrap;
	justify-content: center;
	width: 100%;
}

.color-change-text {
	color: var(--peachpay-theme-color);
	text-transform: uppercase;
	font-weight: bold;
}

.pp-accordion-arrow {
	display: flex;
	height: 12px;
	transition: all 0.3s;
	filter: invert(76%) sepia(0%) saturate(653%) hue-rotate(243deg) brightness(84%) contrast(86%);
}

.pp-new-field textarea,
.pp-new-field input[type='text'] {
	font-family: inherit;
	padding: 30px 16px 8px;
	font-size: 16px;
	resize: none !important;
	width: 100%;
	border: 1px solid #9e9e9e;
	border-radius: 4px;
	appearance: none !important;
	outline: none;
	display: flex;
	margin: 0;
	transition: all 0.3s;
}

.pp-new-field textarea:focus,
.pp-new-field input[type='text']:focus {
	border: 1px solid var(--peachpay-theme-color);
	transition: all 0.3s;
}

.pp-new-field textarea:focus + .pp-form-label,
.pp-new-field textarea:not(:placeholder-shown) + .pp-form-label {
	transform: translate3d(16px, 8px, 0) scale(0.9);
}

.pp-form-checkbox:checked {
	border-color: var(--peachpay-theme-color);
	background-color: var(--peachpay-theme-color);
}

.pp-form-checkbox:checked::after {
	transform: scale(1);
	transition-delay: 0.1s;
	opacity: 1;
}

.pp-form-checkbox {
	width: 1.1rem;
	height: 1.1rem;
	transition: all 0.2s ease-in-out;
	position: relative;
	cursor: pointer;
	vertical-align: -4px;
	border: 1px solid;
	border-radius: 4px;
	margin: 0;
	color: inherit;
	font: inherit;
	padding: 0;
	border-color: #aeaeae;
	background-color: #f4f4f4;
	appearance: none;
}

.pp-form-checkbox::after {
	content: '';
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: scale(0.2);
	transition: all 0.2s ease-in-out;
	opacity: 0;
	width: 10px;
	height: 8px;
	margin-left: -4.4px;
	margin-top: -4.2px;
	background-image: url('../img/white-tick.svg');
}

@media only screen and (max-width: 900px) {
	.pp-new-field textarea {
		font-size: 16px !important;
		line-height: 100% !important;
	}
}

@media only screen and (max-width: 900px) {
	.pp-new-field textarea {
		font-size: 16px !important;
		line-height: 100% !important;
	}
}

.pp-new-field textarea::placeholder {
	font-family: avenir, sans-serif;
}

.pp-shipping-options-container {
	margin-bottom: 15px;
	overflow: hidden;
}

.pp-radio-line {
	display: flex;
	align-items: center;
	gap: 10px;
	font-size: 16px;
	line-height: 26px;
	color: #9e9e9e;
}

.pp-shipping-option-row {
	position: relative;
	padding: 8px 16px 8px 20px;
}

.pp-shipping-option-bg {
	position: absolute;
	pointer-events: none;
	top: 0;
	left: -1px;
	width: calc(100% + 2px);
	height: 100%;
	opacity: 0.15;
}

.pp-shipping-option-row:first-child .pp-shipping-option-bg {
	top: -1px;
	height: calc(100% + 1px);
}

.pp-shipping-option-row:last-child .pp-shipping-option-bg {
	height: calc(100% + 1px);
}

.pp-shipping-option-row.fill .pp-shipping-option-bg {
	background-color: var(--peachpay-theme-color);
}

.pp-shipping-option-row:hover .pp-shipping-option-bg {
	background-color: var(--peachpay-theme-color);
	opacity: 0.05;
}

.pp-radio-line input {
	appearance: none;
	font: inherit;
	color: var(--peachpay-theme-color);
	width: 17px;
	height: 17px;
	border: 0.15em solid var(--peachpay-theme-color);
	border-radius: 50%;
	display: flex;
	place-content: center;
	padding: 2px;
	margin: 0;
	resize: none;
	aspect-ratio: 1;
	flex-shrink: 0;
	cursor: pointer;
}

.pp-radio-line input::before {
	content: '';
	display: flex;
	width: 100%;
	height: 100%;
	border-radius: 50%;
	transform: scale(0);
	transition: all 0.1s;
	box-shadow: inset 1em 1em var(--peachpay-theme-color);
}

.pp-radio-line input:checked::before {
	transform: scale(1);
}

.pp-radio-line label {
	display: flex;
	justify-content: space-between;
	color: #212121;
	cursor: pointer;
}

/* Returning customer order summary list and cost */

.pp-order-list {
	height: auto;
	scrollbar-width: thin;
	padding: 0 16px;
	border-radius: 12px;
	border: 1px solid #e0e0e0;
}

.pp-order-list::-webkit-scrollbar {
	width: 5px;
}

.pp-order-list::-webkit-scrollbar-track {
	background-color: rgb(196 196 196 / 25%);
	border-radius: 100px;
}

.pp-order-list::-webkit-scrollbar-thumb {
	background: #c4c4c4;
	border-radius: 100px;
}

.pp-summary-mobile {
	width: 100%;
	border-collapse: collapse;
}

.pp-summary-mobile td {
	background-color: #fff !important;
}

.pp-summary-mobile tr td:last-child {
	margin-left: 4px;
	margin-top: -12px;
}

.pp-currency-mobile {
	display: none;
}

/* Configuring product image */

.pp-product-img-size {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.pp-bundle-img-size {
	width: 2.5rem;
	height: 2.5rem;
	vertical-align: middle;
	margin: 5px 0;
}

.pp-spacer {
	display: flex;
	flex-direction: column;
	flex: 1 0 32px;
}

.pp-spacer-mobile {
	display: none;
}

@media only screen and (max-width: 900px) {
	.pp-spacer-mobile {
		display: flex;
		flex-direction: column;
		flex: 1 0 24px;
	}

	.pp-spacer {
		display: none;
	}

	.pp-section-mt {
		margin-top: 24px;
	}

	.pp-section-mb {
		margin-bottom: 24px;
	}

	.pp-top-section.mobile {
		display: flex;
		flex-direction: column;
		margin-top: 8px;
	}

	.pp-currency-mobile {
		display: flex;
		flex-direction: column;
	}
}

.pp-test-mode #pp-modal-content {
	border: 3px solid var(--peachpay-test-mode-color);
}

.test-mode-banner {
	display: flex;
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	justify-content: center;
	align-items: center;
	font-size: 0.9rem;

	span {
