.pp-slide-up-view {
	/* transition: <transition type> <transition duration> <transition style> <transition delay> */

	/* the transition below controls behavior going back down */
	transition: transform 0.3s ease 0.15s, background-color 0.2s ease 0s;
	z-index: 901;
	left: 0;
	display: none;
	background-color: rgb(0 0 0 / 0%);
	flex-direction: column;
	width: 612px;
	transform: translate3d(0, 100vh, 0);

	@supports (height: 100dvh) {
		transform: translate3d(0, 100dvh, 0);
	}
}

