@mixin slide-up-mobile {
	.pp-slide-up-view {
		display: flex;
		justify-content: flex-end;
		position: fixed;
		bottom: 0;
		width: 100%;
		height: 100dvh;
	}

	.pp-slide-up-view.expanded,
	.pp-test-mode .pp-slide-up-view.expanded {
		background-color: rgb(0 0 0 / 15%);
		top: 0;
		height: 100%;
		transition: background-color 0.2s linear 0.4s, transform 0.3s ease 0.2s;
		transform: translate3d(0, 0, 0);
		position: absolute;
	}

	.pp-slide-up-header {
		opacity: 1;
		justify-content: space-between;
	}

	.pp-slide-up-view-bg {
		background-color: transparent;
		display: flex;
		height: 100%;
		width: 100%;
	}

	.pp-slide-up-inner {
		height: fit-content;
		max-height: 85%;
		border-radius: 16px 16px 0 0;
		box-shadow: 0 -1px 12px rgb(0 0 0 / 30%);
		position: absolute;
		bottom: 0;
		padding: 16px 16px 134px;

		@supports (height: 100dvh) {
