	margin-bottom: 10px;
}

@media only screen and (max-width: 400px) {
	.flex-left {
		padding: 24px 8px 0 !important;
	}

	.flex-right {
		padding: 0 8px !important;
	}

	#pp-checkout {
		padding: 0;
	}

	form.pp-form {
		margin-bottom: 24px;
	}

	.pp-form select,
	strong {
		font-size: 13px;
	}

	.pp-form .pp-form-label {
		font-size: 14px;
		line-height: 16px;
		transform: translate3d(8px, 13px, 0);

		/* transform: translate3d(8px, 9px, 0px); */
	}

	.pp-form .text-input,
	.pp-form select {
		padding: 18px 8px 6px;
	}

	.pp-form .text-input:focus + .pp-form-label,
	.pp-form .text-input:not(:placeholder-shown) + .pp-form-label,
	.pp-form .pp-select-label {
		transform: translate3d(8px, 6px, 0) scale(0.8);

		/* transform: translate3d(8px, 1px, 0px) scale(0.8); */
	}

	.pp-form .pp-radio-line {
		font-size: 13px;
	}

	.pp-checkout-status-text {
		font-size: 13px;
	}

	.pp-shipping-option-row {
		font-size: 14px;
	}
}

.pp-form-label.radio {
	position: unset;
	transform-origin: unset;
	transition: unset;
	pointer-events: unset;
	transform: unset;
}

.radio-wrapper {
	input[type='radio'] {
		margin: 0 5px 0 0;
	}

	label {
		margin: 0 5px 0 0;
	}
}

.pp-merchant-logo-container {
	height: max-content;
	-webkit-user-drag: none;
	display: inline-flex;

	&.pp-hide-for-mobile {
		margin-bottom: 0.5rem;
	}
}

.pp-merchant-logo-container img {
	display: block;
	max-height: 50px;
	max-width: 100%;
	width: auto;
	height: auto;
	object-fit: cover;
}

.square-btn-container {
	height: 55px;
	display: flex;
	flex-direction: column-reverse;
	justify-content: center;
}

.square-custom-btn-container {
	height: 55px;
	width: 15rem;
	margin: auto;

	& > div {
		display: flex;
		justify-content: center;
	}
}

.square-spinner-container,
.authnet-spinner-container,
.stripe-spinner-container,
.poynt-spinner-container,
.peachpay-integrated-spinner-container {
	height: 55px;
	display: flex;
	justify-content: center;
}

.authnet-btn-container,
.stripe-btn-container,
.poynt-btn-container,
.peachpay-integrated-btn-container {
	height: 55px;
	display: flex;
	justify-content: center;
}
