#pp-pms-new {
	min-height: 110px;
}

#pp-pms-new hr {
	margin: 10px 0;
	border: unset;
	border-top: 0.5px solid #e6e6e6;
}

.pp-pms {
	position: relative;
	display: flex;
	gap: 8px;
	flex-direction: column;
	transition: max-height 0.5s;
	height: fit-content;
	max-height: 1000px;
	width: 100%;
}

.pp-pms .header {
	display: flex;
	flex-direction: row;
	transition: max-height 0.4s;
	max-height: 4.6rem;
	gap: 16px;
}

/* Overriding the default .hide behavior to make a smoother transition */
.pp-pms .header.hide {
	display: flex !important;
	overflow: 0;
	max-height: 0;
	visibility: hidden;
}

.pp-pm-type {
	cursor: pointer;
	display: flex;
	flex-direction: column;
	border: 1px solid #e6e6e6;
	border-radius: 5px;
	flex-grow: 1;
	flex-basis: 0;
	margin: 0;
	padding: 10px;
	font-size: 14px;
	font-weight: 600;
	transition: background-color 0.15s ease, border 0.15s ease, box-shadow 0.15s ease;
	color: #6d6e78;
	box-shadow: 0 1px 1px rgb(0 0 0 / 3%), 0 3px 6px rgb(0 0 0 / 2%);
}

.pp-pm-type.pp-more-options {
	flex-grow: 0;
	justify-content: center;
	padding: 10px 5px;
}

.pp-pm-type.selected {
	cursor: unset;
	background-color: var(--peachpay-theme-color-light);
	border-color: var(--peachpay-theme-color);
	color: var(--peachpay-theme-color);
	box-shadow: 0 1px 1px rgb(0 0 0 / 3%), 0 3px 6px rgb(0 0 0 / 2%), 0 0 0 1px var(--peachpay-theme-color);
}

.pp-pms .header .pp-pm-type:first-child {
	margin-left: 0;
}

.pp-pms .header .pp-pm-type:last-child {
	margin-right: 0;
}

img.pp-pm-full-badge {
	height: 1.2em;
	width: 1.2em;
}

img.pp-pm-more-options {
	height: 16px;
}

.pp-pm-saved-option {
	cursor: pointer;
	border: 1px solid #e6e6e6;
	border-radius: 4px;
	padding: 16px;
	margin: 6px 0;
	position: relative;
	transition: box-shadow 0.2s, border 0.2s;
}

.pp-pm-saved-option[data-gateway='peachpay_purchase_order'] {
	padding: 0;
}

.pp-pm-type:hover:not(.selected) {
	border-color: rgb(148 148 148);
	color: black;
}

.pp-pm-type.pp-pm-alert {
	&::before {
		content: url('../img/exclamation-point.svg');
		margin: -16px 0 -8px 100%;
		opacity: 0;
		transition: opacity 0.2s ease;
	}

	&:hover,
	&.selected {
		background: #ffebee;
		border: 1px solid #c13e3d;

		&::before {
			opacity: 1;
		}
	}
}

.currency-fallback-description {
	.pp-pm-full-badge {
		height: auto;
	}

	.currency-fallback-button {
		font-weight: bold;
		width: auto;
		padding: 4px 8px;
		color: #c13e3d;
		border: 1px solid #c13e3d;
		background-color: white;

		&:hover,
		&:focus {
			color: #c13e3d !important;
			border: 1px solid #c13e3d !important;
			opacity: 1;
		}
	}
}

/* These line up well for mobile too, so no need for an @media */
form.pp-purchase-order-field input[name='purchase_order_number'] + label.pp-form-label {
	transform: translate3d(1rem, -2.5rem, 0);
}

form.pp-purchase-order-field input[name='purchase_order_number']:focus + label.pp-form-label,
form.pp-purchase-order-field input[name='purchase_order_number']:not(:placeholder-shown) + label.pp-form-label {
	transform: translate3d(1rem, -2.95rem, 0) scale(0.8);
}

form.pp-purchase-order-field input[name='purchase_order_number'] {
	padding: 1.5rem 1rem 0.5rem;
	background-color: #f2f2f2;
	color: #43454b;
	border: 0;
	appearance: none;
	outline: none;
	box-sizing: border-box;
	font-weight: normal;
	font-size: 16px;
	line-height: 24px;
	box-shadow: none;
	border-radius: 5px;
	width: 100%;
	margin: 0;
}

span.pp-pm-more-container {
	position: relative;
}

ul.pp-pm-more {
	position: absolute;
	top: 0.9rem;
	right: -4px;
	padding: 0.5rem 0;
	margin: 0.25rem 0 0;
	border-radius: 0.2rem;
	overflow: hidden;
	z-index: 100001;
	background-color: white;
	list-style-type: none;
	color: black;
	box-shadow: 0 0 5px 1px rgb(143 143 143);
}

ul.pp-pm-more li {
	transition: all 0.2s;
	padding: 0.2rem 0.4rem 0.2rem 0.2rem;
	white-space: nowrap;
	cursor: pointer;
	font-size: 14px;
	text-align: left;
}

ul.pp-pm-more li span,
ul.pp-pm-more li img {
	display: inline-block;
	vertical-align: middle;
}

ul.pp-pm-more li:hover {
	background-color: rgb(233 233 233);
}

img.pp-more-option-badge {
	height: 16px;
	margin: 0 0.4rem;
}

@media only screen and (max-width: 900px) {
	#pp-pms-new {
		min-height: unset;
	}

	.pp-pm-type {
		font-size: 12px;
	}

	img.pp-pm-full-badge {
		width: 1.8em;
		height: 1.8em;
	}
}

@media only screen and (max-width: 400px) {
	.pp-pm-type {
		font-size: 11px;
	}
}
