form.coupon-code {
	display: flex;
	position: relative;
}

.pp-top-section form.coupon-code {
	margin-right: -12px;
}

.coupon-code-apply-text,
.gift-card-apply-text {
	margin: 0 0 0 -1px;
	appearance: none !important;
	padding: 8px;
	height: 100%;
	line-height: 24px;
	border: none;
	display: flex;
	align-self: center;
	cursor: grab;
	color: white;
	font-size: 14px;
	text-transform: uppercase;
	border-radius: 0 4px 4px 0;
	background-color: var(--peachpay-theme-color);
}

.coupon-code-apply-text:hover,
.gift-card-apply-text:hover {
	cursor: pointer;
}

.invalid-coupon {
	color: var(--peachpay-theme-color);
	font-size: 12px;
	margin-left: 7%;
}

.coupon-spinner {
	appearance: none !important;
	background-color: transparent;
	position: absolute;
	top: 0;
	right: 3.5rem;
	height: 100%;
	box-sizing: border-box;
	padding: 12px;
}

.pp-apply-code-sm {
	font-size: 14px;
	line-height: 18px;
	font-weight: 700;
	color: #212121;
	cursor: pointer;
	display: flex;
	align-self: center;
	white-space: nowrap;
}

.pp-apply-code {
	margin-top: -1px;
	font-size: 11px;
	color: #828282;
	border-bottom: 1px solid #c5c5c5;
	border-top: 1px solid #c5c5c5;
	padding-top: 5px;
	padding-bottom: 5px;
	cursor: pointer;
	border-collapse: collapse;
}

.pp-apply-code.mobile {
	display: none;
}

.remove-right-border {
	border-right: 0;
	border-radius: 0.5rem  0 0 0.5rem;
}

@media only screen and (max-width: 900px) {
	.invalid-coupon {
		margin-left: 9%;
		margin-bottom: 0 !important;
	}

	.pp-apply-code {
		display: none;
	}

	.pp-apply-code.mobile {
		display: initial;
	}

	.option-mobile {
		background-color: #f3f3f3;
		padding: 0.5rem;
		border-radius: 0.5rem;
	}

	.options-mobile-new {
		text-align: center;
		border: 0;
		padding: 0;
	}

	.wc-coupon-code,
	.pw-wc-gift-card {
		padding-right: 0.5rem;
	}
}
