#pp-ocu-container {
	position: relative;
}

#pp-ocu-backdrop {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
	align-items: safe center;
	justify-content: safe center;
	background-color: rgb(0 0 0 / 80%);
}

#pp-ocu-body {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 10px;
	width: 100%;
	max-width: 600px;
	background-color: white;
	padding: 16px;
	border-radius: 5px;
	position: relative;
}

.pp-ocu-x {
	position: absolute;
	right: 10px;
	top: 10px;
	width: 30px;
	height: 30px;
	padding: 0;
	margin: 0;
	border: none;
	background-color: transparent;
	font-size: 30px;
	cursor: pointer;
	transition: color 0.3s;

	&:hover {
		color: red;
	}
}

.pp-ocu-headline {
	font-size: 40px;
	text-align: center;
}

.pp-ocu-sub-headline {
	font-size: 16px;
	text-align: center;
}

.pp-ocu-product-img {
	object-fit: cover;
	height: 300px;
	width: 100%;
}

.pp-ocu-product-name {
	font-size: 32px;
	font-weight: bold;
	text-align: center;
}

.pp-ocu-product-description {
	font-size: 16px;
	text-align: center;
}

.pp-ocu-product-price {
	font-size: 32px;
	text-align: center;

	& ins {
		text-decoration: none;
	}

	& del {
		color: #6d6d6d;
		opacity: 0.5;
	}
}

.pp-ocu-accept-button {
	height: 64px;
	width: 224px;
	max-width: 100%;
	color: white;
	font-weight: bold;
	border-radius: 5px;
	font-size: 24px;
	cursor: pointer;
	padding: 8px 16px;
	border: none;
	background-color: var(--peachpay-theme-color);
	display: flex;
	align-items: center;
	justify-content: center;

	&:hover {
		opacity: 0.8;
	}
}

.pp-ocu-decline-button {
	font-size: 16px;
	cursor: pointer;
	border: 0;
	background-color: white;
	padding: 0;
	opacity: 0.55;
}

@keyframes pp-blow-up-open {
	0% {
		transform: scale(0);
	}

	100% {
		transform: scale(1);
	}
}

@keyframes pp-blow-up-close {
	0% {
		transform: scale(1);
		opacity: 1;
	}

	100% {
		transform: scale(0);
		opacity: 0;
	}
}

@media only screen and (max-width: 900px) {
	.pp-ocu-headline {
		font-size: 30px;
	}

	.pp-ocu-product-price {
		font-size: 30px;
	}

	.pp-ocu-product-name {
		font-size: 24px;
	}

	.pp-ocu-product-description {
		font-size: 14px;
	}

	.pp-ocu-accept-button {
		font-size: 20px;
	}
}
