#pp-related-products-section {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin: 16px 0;
}

#pp-related-products-container {
	display: flex;
	position: relative;
	width: 100%;
	align-items: center;
	transform-origin: top;
	transition: all 0.25s ease;
	overflow: hidden;
}

#pp-products-list-related-main {
	display: flex;
	gap: 12px;
	padding: 0 8px 12px 0;
}

#pp-products-list-related {
	display: inline-box;
	overflow-x: scroll;
	scroll-snap-type: x mandatory;
	scroll-behavior: smooth;
}

.pp-rp-fade-left {
	pointer-events: none;
	position: absolute;
	top: 0;
	left: -1px;
	width: 10%;
	height: 100%;
	background-image: linear-gradient(to left, rgb(255 255 255 / 0%), white);
	transition: all 0.2s;
}

.pp-rp-fade-right {
	pointer-events: none;
	position: absolute;
	top: 0;
	right: 0;
	width: 10%;
	height: 100%;
	background-image: linear-gradient(to right, rgb(255 255 255 / 0%), white);
	transition: all 0.2s;
}

.pp-rp-fade-left-hide {
	opacity: 0;
	transition: all 0.2s;
}

.pp-rp-fade-right-hide {
	opacity: 0;
	transition: all 0.2s;
}

.pp-rp-scroll-btn {
	width: 16px;
	height: 32px;
	cursor: pointer;
}

.pp-next-btn-related {
	transform: rotate(180deg);
}

.pp-related-product-body,
.pp-product-body {
	background-color: white;
	min-width: 240px;
	display: flex;
	flex-direction: column;
	gap: 8px;
	padding: 8px 16px;
	position: relative;
	border-radius: 8px;
	border: 1px solid #bdbdbd;
}

.pp-related-product-img {
	width: 50px;
	height: 62px;
	object-fit: cover;
}

.pp-rp-content {
	display: flex;
	flex-direction: row;
	font-size: 12px;
	line-height: 16px;
	color: black;
	gap: 8px;
	width: 100%;
}

.pp-rp-title {
	margin-bottom: 4px;
	color: #212121;
}

.pp-rp-sale {
	width: 24px;
	height: 24px;
	margin-right: 10px;
}

.pp-rp-price {
	color: #212121;
	font-size: 14px;
	line-height: 18px;
	font-weight: 700;
	letter-spacing: 1px;
	display: flex;
	flex-direction: row;
	gap: 2px;
	align-items: center;
}

.pp-rp-price-sale {
	flex-direction: row-reverse;
	gap: 10px;
}

.pp-rp-bv-sale {
	flex-direction: row-reverse;
	font-size: 12px;
	font-weight: 600;
	margin-left: -2px;
}

.pp-rp-price ins {
	text-decoration: none;
}

.pp-rp-price del {
	color: #757575;
	font-size: 10px;
}

.pp-rp-add-btn,
.pp-lp-btn {
	display: flex;
	justify-content: center;
	box-sizing: border-box;
	background-color: transparent;
	color: var(--peachpay-theme-color);
	border: 1px solid var(--peachpay-theme-color);
	border-radius: 8px;
	cursor: pointer;
	font-size: 12px;
	line-height: 16px;
	width: 100%;
	padding: 8px 12px;
	gap: 12px;
	text-transform: uppercase;
}

.pp-products-list-related-mobile {
	display: inline-flex;
	gap: 12px;
	padding-bottom: 12px;
	width: 100%;
	overflow-x: auto;
}

.pp-variation-form {
	display: flex;
	flex-direction: column;
	width: 100%;
	gap: 6px;
}

.pp-variation-select-field {
	position: relative;
}

.pp-variation-form label {
	color: #9e9e9e;
	display: block;
	position: absolute;
	transform: translate3d(7px, 2px, 0);
	transform-origin: 0 0;
	cursor: text;
	pointer-events: none;
	font-size: 12px;
}

.pp-variation-form select {
	padding: 16px 10px 4px;
	box-sizing: border-box;
	font-weight: normal;
	font-size: 16px;
	box-shadow: none;
	border-radius: 4px;
	color: #43454b;
	background-color: #f4f4f4;
	display: flex;
	text-overflow: ellipsis;
	overflow: hidden !important;
	width: 100%;
	border: none;

	&:hover {
		cursor: pointer;
	}
}

.pp-lp-form-container {
	gap: 8px;
	align-items: center;
}

.pp-variation-cancel-btn {
	background-color: transparent;
	border: 0;
	cursor: pointer;
	opacity: 0.8;
	font-size: 12px;
}

@media only screen and (max-width: 900px) {
	.pp-related-product-body,
	.pp-product-body {
		width: 220px;
	}

	.pp-rp-price {
		font-size: 12px;
	}

	.pp-rp-price-sale {
		gap: 6px;
	}

	.pp-rp-sale {
		margin-right: 6px;
	}

	.pp-rp-add-btn,
	.pp-lp-btn {
		padding: 4px 8px;
		gap: 4px;
	}

	.pp-variation-form {
		grid-template-columns: unset;
	}

	.pp-lp-form-container {
		align-items: unset;
	}
}
