// CSS for PayPal hosted pay buttons
.paypal-pay-btn-container {
	width: 15rem;
	height: 55px;
	margin: 0 auto;
}

.paypal-pay-spinner-container {
	height: 55px;
	display: flex;
	justify-content: center;

	.paypal-pay-spinner {
		width: 30px;
		height: 55px;
		display: inline-block;
		vertical-align: middle;
	}
}

